import React, {useState} from 'react';
import './App.scss'
import {useNuiEvent} from "../hooks/useNuiEvent";
import {debugData} from "../utils/debugData";
import {fetchNui} from "../utils/fetchNui";

import Textarea from 'react-expanding-textarea'

import {CSSTransition} from 'react-transition-group'

debugData([ { action: 'setVisible', data: true, } ])

interface Rows {
  id: number
  label: string
  helper: string
}

interface MenuData {
  header: string
  rows: Rows[]
}

const App: React.FC = () => {
  
  const [visible, setVisible] = useState(false)
  
  const [title, setTitle] = useState("Create Group")
  const [desc, setDesc] = useState('This form is not a replacement for ticket reporting. You can use this to quickly report a player if you wish to handle the report after the scenario/roleplay has finished.')
  const [rows, setRows] = useState([

    { label: "Who are you reporting?", helper: 'ID, Name, etc...', input: '' },
		{ label: "Why are you reporting these people?", helper: 'Reason for report', input: '' },

  ])

  useNuiEvent<boolean>('setVisible', setVisible)

  useNuiEvent<MenuData>('MenuData', (data: any) => {
    setTitle(data.header)
    setRows(data.rows)
    setDesc(data.desc || '')
  })

  const handleChange = (e: any, key: number) => {

    const currentRows = [...rows]
    currentRows[key] = { ...currentRows[key], input: e.target.value }

    setRows(currentRows)

  }

  const submit = () => {
    fetchNui('submit', rows)
  }

  const cancel = () => {
    fetchNui('cancel')
  }

  return (
    <div className="nui-wrapper">
      <CSSTransition in={visible} timeout={200} classNames="animation" unmountOnExit>
        <div className="mainContainer">
          <div className="header">
            <span>{title}</span>
          </div>
          <div className="form">

            { desc !== '' && <div className='desc'>{desc}</div> }

            {rows.map((data: any, key: number) => {
              return <section style={{ marginTop: 15 }} key={key}>
              <span>{data.label}</span>

              <Textarea
                className='input'
                value={data.input}
                onChange={ (e) => handleChange(e, key) }
                placeholder={data.helper}
              />

            </section>
            })}

            <div className="buttons">
              <div className="var2" onClick={cancel} > CANCEL </div>
              <div className="var1" onClick={submit} > Confirm </div>
            </div>

          </div>
          
        </div>
      </CSSTransition>
    </div>
  );
}

export default App;
